.rakuie-ceo {
  padding: 80px 30px;
  background: #efefef;
}

.rakuie-ceo.is-inview .inner p,
.rakuie-ceo.is-inview .inner img {
  opacity: 1;
  visibility: visible;
  transform: translateY(0);
}

.rakuie-ceo .inner {
  width: 1060px;
  margin: 0 auto;
  text-align: center;
}

.rakuie-ceo .inner p {
  display: inline-block;
  margin-bottom: 40px;
  padding: 0 30px;
  font-size: 2.8rem;
  font-weight: bold;
  line-height: 1.5em;
  text-align: center;
  position: relative;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: 0.7s ease;
  transition-delay: 0.3s;
}

.rakuie-ceo .inner p::before, .rakuie-ceo .inner p::after {
  content: '';
  display: block;
  width: 3px;
  height: 60px;
  background: #333;
  position: absolute;
  bottom: -10px;
}

.rakuie-ceo .inner p::before {
  left: 0;
  transform: rotate(-20deg);
}

.rakuie-ceo .inner p::after {
  right: 0;
  transform: rotate(20deg);
}

.rakuie-ceo .inner img {
  display: block;
  width: 100%;
  opacity: 0;
  visibility: hidden;
  transform: translateY(20px);
  transition: 0.7s ease;
  transition-delay: 0.5s;
}

@media screen and (max-width: 767px) {
  .rakuie-ceo {
    padding: 10.66667vw 5.33333vw;
  }
  .rakuie-ceo .inner {
    min-width: auto;
    width: 100%;
  }
  .rakuie-ceo .inner p {
    margin-bottom: 5.33333vw;
    padding: 0 5.33333vw;
    font-size: 1.6rem;
  }
  .rakuie-ceo .inner p::before, .rakuie-ceo .inner p::after {
    width: 2px;
    height: 8vw;
    bottom: -1.33333vw;
  }
}
